import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Shiftbadge from './shiftbadge';
import Addbadge from '../pages/addbadge';
import {connect} from 'react-redux';
import * as actionTypes from '../store/actions/action';
import { Droppable } from 'react-drag-and-drop';


class Assignedshift extends Component { 
    state = {
        isDropable : this.props.editable !== "true" ?  'dropable-era' : 'border border-white',
        isAllowedRange : new Date().toISOString().slice(0,10)
          
    }
    updateClass = () => {
        [...document.getElementsByClassName('dropableGrow')].map(item => {
            if(!item.classList.contains('SmoothEntire') && item.clientWidth == 0) {
                item.classList.add('SmoothEntire');
                item.classList.remove('py-4');
            }
            
        })
    }
    componentDidMount(){ } 
    componentDidUpdate(){
        this.updateClass();
    }
    generateBadges = (arr,range,type) => {
        if(arr!==undefined){
            const badge = 
            arr.map((item,index) => (
                range === item.date
            ?  <Shiftbadge 
                isDragable="true"
                acceptdate={range}
                key={index} userid={this.props.userid} 
                shifts={item.shifts} parentview={this.props.editable} 
                shiftName={this.props.shiftName} owndate={item.date} 
                ownpositionid={this.props.positionid}>
                    {type === 'creator' 
                    ? <div className="editbadges">
                        <Addbadge
                            textcolor="text-white"
                            acceptdate={range}
                            positionid={this.props.positionid}
                            userid={this.props.userid}
                            date={range}
                            time="11:30PM - 7:30AM"
                        />                        
                    </div>
                    : null                   
                    }
                </Shiftbadge>     
            : null            
            ))

            return badge;
        }  
    }
    onDrop = (data,event) => {
        const [positionid] = Object.keys(data);
        const [value] = Object.values(data); 
        const valueList = `${value}`.split(',');
        const [uid,index,shiftName,fromdate,infoname,infother] = valueList;
        const userid = event.target.getAttribute('data-userid');
        const todate = event.target.getAttribute('data-todate');
        const pos = positionid.toUpperCase();
       
        const record = {
            name: infoname,
            info: infother
        }
        if(uid === userid && positionid!=='' && fromdate === todate){
            alert('not allowed');
        }
        else {
            if(uid===''){
                //drag from parent to own different users
                this.props.onMoveBadge(record,index,shiftName,pos,todate,userid);
                this.props.onMovePosition(record,index,shiftName,pos,todate,userid,fromdate);               
            }
            else {
                //drag between different users in same parent
                this.props.onMoveBadge(record,index,shiftName,pos,todate,userid);
                this.props.onMoveBadgeOnBadgeToRemove(index,userid,fromdate);
            }
        }
        if(event.target.classList.contains('SmoothEntire')){
            event.target.classList.remove('SmoothEntire');
        }
        if(!event.target.classList.contains('py-4')){
            event.target.classList.add('py-4');
        }
    }
    render(){
       
        const result = this.props.week.length !== 0 
          ? (
            <Row>
                {
                    this.props.week.map((itemM,index)=>(
                        <Col key={itemM.rangewhole} 
                        className='d-flex justify-content-center d-flex align-items-end p-0 border border-white
                        border-top-0 border-bottom-0 minsize'>
                            <div>                            
                                {                                    
                                   this.props.editable !== "true"
                                    ? 
                                    <div> 
                                         <div className={this.state.isDropable}>
                                         {
                                             itemM.rangewhole >= this.state.isAllowedRange ?
                                            <Droppable
                                                types={this.props.positionid.toLowerCase()}
                                                onDrop={this.onDrop.bind(this)}>
                                                <div className={
                                                    this.props.assignShifts.filter(result => result.date === itemM.rangewhole).length != 0
                                                    ? 'py-4 dropableGrow'
                                                    : 'SmoothEntire dropableGrow'
                                                }
                                                data-todate={itemM.rangewhole} 
                                                data-userid={this.props.userid}></div>
                                            </Droppable> : null
                                         }
                                        </div>
                                    </div>
                                    : null
                                }
                                {
                                    //editable meaning parent row able to create badges
                                    this.props.editable === "true"
                                    ? this.generateBadges(this.props.createShifts,itemM.rangewhole,'creator')
                                    : this.generateBadges(this.props.assignShifts,itemM.rangewhole,'getter')
                                }
                                
                                {
                                    this.props.editable === "true" && this.props.createShifts!==undefined &&
                                    this.props.createShifts!==undefined && this.props.createShifts.filter(result => result.date === itemM.rangewhole && result.shifts.length!==0).length === 0
                                    ? 
                                    itemM.rangewhole >= this.state.isAllowedRange ?
                                        <Addbadge
                                        textcolor="text-dark"
                                        acceptdate={itemM.rangewhole}
                                        positionid={this.props.positionid}
                                        userid={this.props.userid}
                                        date={itemM.rangewhole}
                                        time="11:30PM - 7:30AM"
                                        /> : null
                                    : null 
                                }
                            </div> 
                        </Col>
                    ))
                }            
            </Row> 
          )
          : null;
        return (
            <div className="Assignedshift">
                <Row>
                    <Col xs={12} md={12} className="">
                        <div>
                            {result}
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}
const mapStateToProps = state  => {
    return {
        week: state.dateReducer.week        
    }
}


const mapDispatchToProps = dispatch => {    
    return {
        onMovePosition : (rec, index, byname,pid,todate,uid,pfromdate) => dispatch({ type:actionTypes.MOVETOASSIGNTOPOSITION,
        payload:{record:rec,index:index,shiftName:byname,todate:todate,pid:pid,uid:uid,fromdate:pfromdate} }),

        onMoveBadge : (rec, index, byname,pid,todate,uid) => dispatch({ type:actionTypes.MOVETOASSIGNTOBADGE,
        payload:{record:rec,index:index,shiftName:byname,todate:todate,pid:pid,uid:uid} }),

        onMoveBadgeOnBadgeToRemove: (index,uid,pfromdate) => dispatch({type:actionTypes.MOVEBADGEONBADGETOREMOVE,
        payload:{index:index,uid:uid,fromdate:pfromdate}})
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Assignedshift);