import * as actionTypes from '../actions/action';

const initialBadgeState = {
    badges : [        
        {
            uid: 'U01',
            assignShifts : []
        },
        {
            uid: 'U02',
            assignShifts : []
        }
        ,
        {
            uid: 'U03',
            assignShifts : []
        }
    ]
}

const badgesReducer = (state = initialBadgeState, action) => {
    switch(action.type){
        case actionTypes.GETBADGEALL:       
        return {
            ...state
        }
        case actionTypes.MOVETOASSIGNTOBADGE:
        //generate shift for existing date 
        const shif = {name: action.payload.record.name, info: action.payload.record.info}
        //use shift for existing/new date
        const payloads = {
            date: action.payload.todate, 
            shifts : [
                shif
            ]
        } 
        return {
            ...state,
            badges: state.badges.map((item) =>
                item.uid === action.payload.uid 
                ? {
                    ...item,
                    assignShifts:
                        item.assignShifts.filter(result => result.date === action.payload.todate).length !==0 ?
                        item.assignShifts.map((itemn) =>                       
                            itemn.date === action.payload.todate
                            ? {
                                ...itemn,
                                shifts: [...itemn.shifts, shif]
                            }
                            : itemn
                        )
                        : [...item.assignShifts, payloads]
                } 
                : item
            )
        } 
        case actionTypes.MOVEBADGEONBADGETOREMOVE:
        return {
            ...state,
            badges: state.badges.map((item) =>
                item.uid === action.payload.uid 
                ? {
                    ...item,
                    assignShifts:
                        item.assignShifts.filter(result => result.date == action.payload.fromdate).length !==0 ?
                        item.assignShifts.map((itemn) =>                       
                            itemn.date === action.payload.fromdate
                            ? {
                                ...itemn,
                                shifts: itemn.shifts.filter((result,index) => index != action.payload.index)
                            }
                            : itemn
                        )
                        : item.assignShifts
                } 
                : item
            )
        } 
        default:
        //console.log('no default');
    }
    return state;
}
export default badgesReducer;