import React, { Component } from 'react';
import {connect} from 'react-redux';
import * as actionTypes from '../store/actions/action';
import { FaTrash } from 'react-icons/fa';

class Shiftdelete extends Component { 
    render(){
        return (
            <div className="Shiftdelete py-2">
                <div className="text-center">
                <FaTrash
                    className="text-white"
                    onClick={() => this.props.onRemoveBadge(this.props.positionid,this.props.owndate,this.props.ownindex)}
                />
                </div>
            </div>
        )
    }
}
const mapDispatchToProps = dispatch => {    
    return {
        onRemoveBadge : (id,date,index) => dispatch({ type:actionTypes.REMOVEBADGE,
             payload:{id:id,date:date,index:index} })
    }
}
export default connect(null,mapDispatchToProps)(Shiftdelete);