import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';

import user1 from '../user1.png';

import Assignedshift from '../pages/assignedshift';
import {connect} from 'react-redux';

class Shiftlayout extends Component { 
    state = {
        currentBadge : null
    }
    componentDidMount(){
       
    } 
    getIndBadges = (id) => {
        const [badgesarray] = [...this.props.badges].filter((res) => res.uid === id);
        return badgesarray.assignShifts
    }
    render(){
        const empPosition = this.props.positions.length !== 0 
            ? (
                this.props.positions.map((item,index) => (
                    <div key={item.id}>
                        <Row className="border border-dark border-top-0 border-left-0 border-right-0">
                        <Col xs={12} md={4} className="p-0">
                            <Card
                                className="rounded-0"
                                bg="info"
                                text="light"
                                style={{ height: '100%' }}
                                >
                                <Card.Body className="pt-0 pb-0">
                                    <Card.Title>{item.shiftName}</Card.Title>
                                    <Card.Text>
                                        {item.type}
                                    </Card.Text>
                                </Card.Body>
                                <Card.Footer className="bg-transparent border-top-0">
                                    <small>
                                        Sheduled hour: {item.schedule}
                                    </small>
                                </Card.Footer>
                            </Card>
                        </Col>
                        <Col xs={12} md={8}>
                            <Assignedshift 
                            editable="true" 
                            positionid={item.id}
                            createShifts={item.createShifts}
                            shiftName={item.shiftName}
                            />
                        </Col>                        
                    </Row>  
                    {
                        item.users.map((itemN) => (
                            <Row key={itemN.uid} className="border border-dark border-top-0 border-left-0 border-right-0">
                                <Col xs={12} md={4} className="border border-dark border-top-0 border-left-0 border-bottom-0 d-flex align-items-center">
                                    <div className="media">
                                        <img className="align-self-center mr-3"
                                        src={user1} alt="user"/>
                                        <div className="media-body">
                                            <h5 className="mt-0">{itemN.info.name}</h5>
                                            {itemN.info.status}, {itemN.info.other}
                                        </div>
                                    </div>
                                    <div className="hourinfo">{itemN.info.totalTime}</div>
                                </Col>
                                <Col xs={12} md={8}>
                                    <Assignedshift 
                                    editable="false"
                                    positionid={item.id}
                                    useripositionsd={itemN.uid}
                                    userid={itemN.uid}
                                    assignShifts={this.getIndBadges(itemN.uid)}
                                    />
                                </Col>
                            </Row>
                        )
                        )
                    }
                    </div>   
                ))
                )
            : null;
        return (
            <div className="Shiftlayout">
                         {empPosition}  
            </div>
        )
    }
}
const mapStateToProps = state  => {
    return {
        positions: state.positionReducer.position,
        badges: state.badgeReducer.badges
    }
}

export default connect(mapStateToProps)(Shiftlayout);