import * as actionTypes from '../actions/action';

const initialPositionState = {
    position : [
        {
            id: 'P01',
            shiftName : 'Dishwasher',
            type: 'Open shift',
            schedule:'6 hrs',
            createShifts : [],	
            users: [
                {	
                    uid: 'U01',
                    info: { 
                        name: 'Anna Brown', 
                        status: 'Full time', 
                        other : 'secondary', 
                        totalTime:'24.0 hrs'
                    }
            
                }
            ]
        },
        {
            id: 'P02',
            shiftName : 'Server',
            type: 'Open shift',
            schedule:'6 hrs',	
            createShifts : [],	
            users: [
                {	
                    uid: 'U03',
                    info: { 
                        name: 'Olivia Miller', 
                        status: 'Full time', 
                        other : 'secondary', 
                        totalTime:'64.0 hrs'
                    }
            
                }
            ]
        }
    ]
}
const getReducedTotalAndRemove = (shifts,indexn) => {
    const rec =
    shifts.map((itemns,index) =>                       
        index == indexn
        ? {
            ...itemns,
            total: itemns.total - 1
        }
        : itemns
    )

    const removshift = rec.filter(result => result.total !== 0);

    return removshift;
}
const reduceTotal = (indexn,pid,date,state) => {
    return {
        ...state,
        position: state.position.map((item) =>
            item.id === pid 
            ? {
                ...item,
                createShifts:
                    item.createShifts.filter(result => result.date === date).length !==0 ?
                    item.createShifts.map((itemn) =>                       
                        itemn.date == date
                        ? {
                            ...itemn,
                            shifts:                            
                            getReducedTotalAndRemove(itemn.shifts,indexn)
                        
                        }
                        : itemn
                    )
                    : item.createShifts
            } 
            : item
        )
    }
}

const positionsReducer = (state = initialPositionState, action) => {
    switch(action.type){
        case actionTypes.GETPOSITIONALL:       
        return {
            ...state
        }        
        case actionTypes.POSTPOSITIONBADGE: 
            //generate shift for existing date 
            const shif = {name: 'Resident Care', info: action.payload.time, total:action.payload.total}
            //use shift for existing/new date
            const payloads = {
                date: action.payload.date, 
                shifts : [
                    shif
                ]
            }  
            
            return {
                ...state,
                position: state.position.map((item) =>
                    item.shiftName === action.payload.name 
                    ? {
                        ...item,
                        createShifts:
                            item.createShifts.filter(result => result.date === action.payload.date).length !==0 ?
                            item.createShifts.map((itemn) =>                       
                                itemn.date === action.payload.date
                                ? {
                                    ...itemn,
                                    shifts: [...itemn.shifts, shif]
                                }
                                : itemn
                            )
                            : [...item.createShifts, payloads]
                    } 
                    : item
                )
            }
            case actionTypes.EDITBADGE:
            return {
                ...state,
                position: state.position.map((item) =>
                    item.id === action.payload.id 
                    ? {
                        ...item,
                        createShifts:
                            item.createShifts.filter(result => result.date === action.payload.date).length !==0 ?
                            item.createShifts.map((itemn) =>                       
                                itemn.date === action.payload.date
                                ? {
                                    ...itemn,
                                    shifts: itemn.shifts.map((val,index) => 
                                    index === action.payload.index
                                    ? {
                                        ...val,
                                        total: action.payload.total
                                    }
                                    : val
                                    )
                                }
                                : itemn
                            )
                            : [...item.createShifts, payloads]
                    } 
                    : item
                )
            }
            case actionTypes.REMOVEBADGE:     
            return {
                ...state,
                position: state.position.map((item) =>
                    item.id === action.payload.id 
                    ? {
                        ...item,
                        createShifts:
                            item.createShifts.map((itemn) =>                       
                            itemn.date === action.payload.date
                            ? {
                                ...itemn,
                                shifts: itemn.shifts.filter((res,index) => index !== action.payload.index)
                            }
                            : itemn
                            )
                    } 
                    : item
                )
            }
            case actionTypes.MOVETOASSIGNTOPOSITION:
                return reduceTotal(action.payload.index, action.payload.pid, action.payload.fromdate,state);    
            
        default:
        //console.log('no default');
    }
    return state;
}
export default positionsReducer;