import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import Addbadge from '../pages/addbadge';
import Shiftdelete from '../pages/shiftdelete';
import Badge from 'react-bootstrap/Badge';
import { Draggable } from 'react-drag-and-drop';
import { FaCopy } from 'react-icons/fa';

class Shiftbadge extends Component { 
    render(){     
        const result = this.props.shifts.length !== 0 
          ? (
              this.props.shifts.map((item,index) => (                
                <Card key={index} border="light" bg="light" className="my-2 mx-1 p-2">                                                           
                    <Card.Body  className="p-0">
                        {
                            this.props.parentview  === "true"
                            ? <div className="toshowHover">                                
                                <div className="d-flex justify-content-around"> 
                                <Addbadge
                                    textcolor="text-white"
                                    ownindex={index}
                                    owndate={this.props.owndate}
                                    owntotal={item.total}
                                    iseditable="true"
                                    shiftName={this.props.shiftName}
                                    positionid={this.props.ownpositionid}
                                />
                                <Shiftdelete
                                    ownindex={index}
                                    owndate={this.props.owndate}
                                    positionid={this.props.ownpositionid}
                                />
                                {this.props.children}   
                                <div className="py-2 text-center">
                                    {                            
                                        this.props.isDragable === "true" 
                                        ? <Draggable type={[this.props.ownpositionid.toLowerCase()]} 
                                            data={[
                                                this.props.userid,
                                                index,
                                                this.props.shiftName,
                                                this.props.owndate,
                                                item.name,item.info
                                            ]}
                                        ><FaCopy className="text-white"/></Draggable>
                                        : null
                                    }
                                </div>                             
                            </div>
                            </div>                            
                            : null

                        } 
                        {
                            this.props.parentview  !== "true" 
                            ? <Card.Subtitle>{item.name}</Card.Subtitle > 
                            : <Card.Subtitle className="badgecontainer"><Badge variant="dark">{item.total}</Badge></Card.Subtitle >
                        }
                       
                        <Card.Subtitle className="mt-2 text-muted">{item.info}</Card.Subtitle>
                        {this.props.parentview  !== "true" ?
                            (<div className="py-2 text-center">
                                    {                            
                                        this.props.isDragable === "true" 
                                        ? <Draggable type={[this.props.ownpositionid.toLowerCase()]} 
                                            data={[
                                                this.props.userid,
                                                index,
                                                this.props.shiftName,
                                                this.props.owndate,
                                                item.name,item.info
                                            ]}
                                        ><FaCopy className="text-dark"/></Draggable>
                                        : null
                                    }
                                </div>)
                                : null
                        }
                    </Card.Body>
                    
                    {/* <Moveassign record={item} userid={this.props.userid} fromdate={this.props.owndate} recordindex={index} ownpositionid={this.props.ownpositionid} shiftName={this.props.shiftName}/> */}
                    
                </Card>    
                ))
            )
          : null;
        return (
            <div className="Assignedshift">
                <Row className="no-gutters">
                    <Col xs={12} md={12} className="bg-white">
                        <div>
                            {result}                                                    
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}
export default (Shiftbadge);