import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import {connect} from 'react-redux';
import * as actionTypes from '../store/actions/action';
import Modal from 'react-bootstrap/Modal';

import { FaEdit } from 'react-icons/fa';
import { FaPlus } from 'react-icons/fa';

import Shiftform from './form';

class Addbadge extends Component { 
    state = {
        show : false,
        setShow : false,
        name: 'Dishwasher',
        total: 1
    }

    //popup handler
    handleClose = () => this.setState({show:false});
    handleShow = () => this.setState({show:true});

    //input handler
    handleChange = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }
    render(){
        const payload = {
            positionid: this.props.positionid,
            userid : this.props.userid,
            acceptdate: this.props.acceptdate,
            date: this.props.date,
            name: this.state.name,
            time: this.props.time,
            total: this.state.total
        }     
        return (
            <div className="Addbadge py-2">
                <div className="text-center">              
                    <div
                    onClick={this.handleShow}
                    className="font-weight-bold"
                    >
                      {this.props.iseditable === "true" ? <FaEdit className={this.props.textcolor}/> : <FaPlus className={this.props.textcolor}/>}
                    
                    </div>
                    <Modal size="sm" backdrop="static" show={this.state.show} onHide={this.handleClose}>
                        <Modal.Header closeButton>
                        <Modal.Title>{this.props.iseditable === "true" ? "Edit badge" : "Create badge"}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body className="show-grid">
                            <Shiftform
                            editable={this.props.iseditable}
                            selectedname={this.props.shiftName}
                            selectedtotal={this.props.owntotal}
                            handleChange={this.handleChange}
                            />                        
                        </Modal.Body>
                        <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Cancel
                        </Button>
                        <Button variant="primary" onClick={() => {
                                this.props.iseditable === "true"
                                ? this.props.onEditBadge(this.props.positionid,this.state.total,this.props.owndate,this.props.ownindex)
                                : this.props.onPostPositionBadge(payload)
                                this.handleClose()
                                }}>
                            Create
                        </Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = dispatch => {    
    return {
        onPostPositionBadge : (payload) => dispatch({ type:actionTypes.POSTPOSITIONBADGE,payload:payload }),
        onEditBadge : (id,total,date,index) => dispatch({ type:actionTypes.EDITBADGE, payload:{id,total:total,date:date,index:index} })
    }
}
export default connect(null,mapDispatchToProps)(Addbadge);