import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import {connect} from 'react-redux';

class Calendar extends Component { 
    render(){
        const result = this.props.value.length !== 0 
          ? (
            <Row>
                {
                    this.props.week.map((item,index)=>(
                        <Col key={index} className="text-center">
                            <div className="text-uppercase">{item.rangeDay} </div>
                            <div>{item.rangeDate}</div>
                        </Col>
                    ))
                }            
            </Row> 
          )
          : <h3>please choose date</h3>;
        return (
            <div className="Calendar  px-1 py-2">
                <Row>
                    <Col xs={12} md={12}>
                        <div>
                        {result}
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}

const mapStateToProps = state  => {
    return {
        value : state.dateReducer.value,
        week: state.dateReducer.week
    }
}
export default connect(mapStateToProps)(Calendar);