import * as actionTypes from '../actions/action';

const initialState = {
    intvalue: [new Date('2020-06-21'), new Date('2020-06-27')],
    value: [new Date('2020-06-21'), new Date('2020-06-27')],
    week:[],
    weekday : ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
}
const getDatesHandler = (startDate, endDate) => {
        let newWeek = [];
        let dt = new Date(startDate);
        while (dt <= endDate) {
            let dd = new Date(dt);        
            newWeek.push({
                rangeDay :initialState.weekday[dd.getDay()],
                rangeDate : dd.getDate(),
                rangewhole: dd.toISOString().slice(0,10)
            });
            dt.setDate(dt.getDate() + 1);
        }
        return newWeek;
}
const reducer = (state = initialState, action) => {
    switch(action.type){
        case actionTypes.CHANGE:
        const [firstn,lastn] = [...action.val];        
        return {
            ...state,
            value : action.val,
            week: getDatesHandler(firstn,lastn)
        }
        case actionTypes.GETDATE:
        const [first,last] = [...state.value];
        return {
            ...state,
            week: getDatesHandler(first,last)
        }
        default:
        //console.log('no default');
    }
    return state;
}

export default reducer;