import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';

class Shiftform extends Component { 
       
    render(){
        const isSelect = (
            this.props.selectedname === undefined  
            ? <Form.Control as="select" name="name"
                defaultValue="Dishwasher"
                onChange={this.props.handleChange}>
                <option>Dishwasher</option>
                <option>Server</option>
            </Form.Control>
            : this.props.selectedname                     
        )
        return (
            <div className="Shiftform">
                <Container>
                            <Row>
                                <Col xs={12} md={8}>
                                    <Form>
                                        <Form.Group controlId="nameForm">
                                            {isSelect}
                                        </Form.Group>
                                    </Form>
                                </Col>
                                <Col xs={6} md={4}>
                                    <Form>
                                        <Form.Group controlId="totalForm">
                                            <Form.Control as="select"
                                            defaultValue={this.props.selectedtotal}
                                            name="total" onChange={this.props.handleChange}>
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </Form>
                                </Col>
                            </Row>
                        </Container>
            </div>
        )
    }
}
export default Shiftform;