import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import 'bootstrap/dist/css/bootstrap.min.css';

import { createStore, combineReducers } from 'redux';
import { Provider } from 'react-redux';
import reducer from './store/reducers/shifts';
import badgesReducer from './store/reducers/badges';
import positionsReducer from './store/reducers/position';

//reducer
const rootReducer = combineReducers({
  dateReducer : reducer,
  badgeReducer : badgesReducer,
  positionReducer: positionsReducer
});


//store
const store = createStore(rootReducer);

//connect store to react

ReactDOM.render(
  <Provider store={store}>
  <App />
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
