import React, { Component } from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { DateRangePicker } from 'rsuite';
import {connect} from 'react-redux';
import * as actionTypes from '../store/actions/action';

class Dates extends Component { 
    
    componentDidMount(){  
        this.props.onGetDates();
    }    

    render(){
        const instance = (
            <div className="field px-1 py-3">
              <DateRangePicker 
              appearance="default"
              value={this.props.value}
              onChange={ value => this.props.onChangeHandler(value) }
              onClean={()=>this.props.onChangeHandler(this.props.intvalue)}
              oneTap hoverRange="week" ranges={[]} />              
            </div>
          );
          
        return (
            <div className="Dates">
                <Row>
                    <Col xs={12} md={12}>
                        <div>
                        {instance}
                        
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}

const mapStateToProps = state  => {
    return {
        intvalue: state.dateReducer.intvalue,
        value : state.dateReducer.value,
        week: state.dateReducer.week,
        weekday: state.dateReducer.weekday
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onChangeHandler : (value) => dispatch({type:actionTypes.CHANGE, val: value}),
        onGetDates : () => dispatch({type:actionTypes.GETDATE})
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Dates);